/**
 * Button Text
 *
 * @see ../config/variables/components/_buttons.scss for $btn variables
 *
 * @since 1.0.0
 */
%#{$btn-class}-text {
	@include font-family( $primary-font );
	text-decoration: none;

	@if $btn-font-size {
		font-size: rem($btn-font-size);
	}

	@if $btn-font-weight {
		font-weight: $btn-font-weight;
	}

	@if $btn-text-transform {
		text-transform: $btn-text-transform;
	}
}

/**
 * Base button properties, minus the colors.
 *
 * Defines the base btn class to contain things like border-radius, padding, etc.
 *
 * @see ../config/variables/components/_buttons.scss for $btn variables
 *
 * @since 1.0.0
 */
%#{$btn-class} {
	@include button;

	// Small version.
	&--sm {
		font-size: rem($btn-font-size-sm);
		padding: rem($btn-vertical-padding-sm) rem($btn-horizontal-padding-sm);
	}

	// Large version.
	&--lg {
		font-size: rem($btn-font-size-lg);
		padding: rem($btn-vertical-padding-lg) rem($btn-horizontal-padding-lg);
	}

	// Fullwidth version.
	&--block {
		display: block;
	}
}

/**
 * Solid Buttons (Primary / Secondary button styles).
 *
 * @see ../config/variables/components/_buttons.scss for $btn-solid-variants variable
 *
 * @since 1.0.0
 */
 @each $modifer, $options in $btn-solid-variants {

	$class: "#{$btn-class}--#{$modifer}";

	// The default / neutral button colors.
	@if $modifer == "neutral" {
		$class: "#{$btn-class}";
	}

	// All other solid button variants (btn--primary, btn--secondary, etc).
	%#{$class} {
		@include button-variant($options...);
	}
}

/**
 * Outline Buttons
 *
 * @see ../config/variables/components/_buttons.scss for $btn-outline-variants variable
 *
 * @since 1.0.0
 */
@each $modifer, $options in $btn-outline-variants {

	$class: "#{$btn-class}--#{$modifer}-o";

	%#{$class} {
		@include button-variant($options...);
	}
}
