$form-required: theme-color("red");

// sass-lint:disable quotes
$input-text:
	'[type="color"]',
	'[type="date"]',
	'[type="datetime"]',
	'[type="datetime-local"]',
	'[type="email"]',
	'[type="month"]',
	'[type="number"]',
	'[type="password"]',
	'[type="search"]',
	'[type="tel"]',
	'[type="text"]',
	'[type="time"]',
	'[type="url"]',
	'[type="week"]'
;
// sass-lint:enable quotes

$input-height: 50;
$input-vertical-padding: 10;
$input-horizontal-padding: 16;
$input-font-size: 16;
$input-font-size-mobile: 16;
$input-font-family: $primary-font;
$input-background-color: #fff;
$input-text-color: $base-font-color;
$input-placeholder-text-color: $gray;
$input-border: $border-gray;
$input-focus-border-color: $tertiary-color;
$input-radius: 0;

$inline-labels: false;
$label-font-size: $input-font-size;
$label-font-family: $input-font-family;
$label-text-color: $input-text-color;
$label-margin-bottom: 5;

$sublabel-text-color: $gray-d;
$sublabel-font-family: $primary-font;
$sublabel-font-size: 14;
$sublabel-margin-top: 5;
$sublabel-margin-bottom: 10;

$checkbox-size: 20;
$checkbox-color: #fff;
$checkbox-border-color: $gray;
$checkbox-border-width: 1;
$checkbox-checked-color: $primary-color;
$checkbox-checked-border-color: $primary-color;
$checkbox-checked-background-color: #fff;
$checkbox-position-top: 9;
$checkbox-check-position-top: 13;
$checkbox-check-position-left: 8;

$radio-center-size: 10;

$select-padding-right: 35;
$select-use-css-caret: true;
$select-css-caret-size: 11px;
$select-css-caret-color: $primary-color;

%required {
	color: $form-required;
	margin-left: 3px;
	position: relative;
	top: -4px;
}
