@mixin center($force: null) {
	@if $force {
		margin-left: auto !important;
		margin-right: auto !important;
	} @else {
		margin-left: auto;
		margin-right: auto;
	}
}

@mixin aligndefault($force: null) {
	@include center($force);
	@include site-gutter($force);
	@if $force {
		max-width: rem($content-width-default--gutters) !important;
	} @else {
		max-width: rem($content-width-default--gutters);
	}

	@include jacket(editor) {

		> .editor-inner-blocks {
			margin: 0 14px;
		}
	}
}

@mixin alignwide($force: null) {
	@include center($force);
	@include site-gutter($force);
	@if $force {
		max-width: rem($content-width-wide--gutters) !important;
	} @else {
		max-width: rem($content-width-wide--gutters);
	}
}

@mixin alignfull($force: null) {
	@if $force {
		max-width: 100% !important;
	} @else {
		max-width: 100%;
	}
}

// Applies the default content width while aligning left with .alignwide elements.
@mixin aligndefault--left {
	@include alignwide;

	@include breakpoint(aligndefault) {
		padding-right: calc(100% - #{rem($content-width-default)});
	}

	@include breakpoint(alignwide) {
		padding-right: rem($content-width-wide - $content-width-default);
	}
}
