/**
 * BUTTONS
 * Default button variables, button variant declarations, helpful button mixins.
 *
 * @since 1.0.0
 */



/**
 * Common variables that all buttons share.
 *
 * @since 1.0.0
 */
$btn-class: "btn";

// Padding
$btn-vertical-padding: 10.5;
$btn-horizontal-padding: 25;

$btn-vertical-padding-sm: $btn-vertical-padding * .8;
$btn-horizontal-padding-sm: $btn-horizontal-padding * .8;

$btn-vertical-padding-lg: $btn-vertical-padding * 1.2;
$btn-horizontal-padding-lg: $btn-horizontal-padding * 1.2;

// Min-width
$btn-min-width: null;

// Font styles
$btn-font-size: $base-font-size;
$btn-font-size-sm: $btn-font-size * .8;
$btn-font-size-lg: $btn-font-size * 1.2;
$btn-font-weight: bold;
$btn-text-transform: null;

// Border styles
$btn-border-radius: 0;
$btn-border-width: 1;

/**
 * Button Variants
 *
 * Variants with a key of "default" won't get a modifier class.
 * The key names must match the arguments of the 'button-variant' mixin, minus the dollar sign ($).
 * Set values to null to avoid extraneous output.
 *
 * @since 1.0.0
 */

// Outline Buttons
$btn-outline-variants: (
	// .#{$btn-class}--primary-o
	"primary": (
		"background-color": rgba( white, 0 ),
		"border-color": $primary-color,
		"text-color": $primary-color,
		"hover-background-color": $primary-color,
		"hover-border-color": $primary-color,
		"hover-text-color": #fff
	),

	// .#{$btn-class}--secondary-o
	"secondary": (
		"background-color": rgba( white, 0 ),
		"border-color": $secondary-color,
		"text-color": $secondary-color,
		"hover-background-color": $secondary-color,
		"hover-border-color": $secondary-color,
		"hover-text-color": #fff
	),

	"tertiary": (
		"background-color": rgba( white, 0 ),
		"border-color": $tertiary-color,
		"text-color": $tertiary-color,
		"hover-background-color": $tertiary-color,
		"hover-border-color": $tertiary-color,
		"hover-text-color": #fff
	),

	// .#{$btn-class}--inverse-o
	"inverse": (
		"background-color": rgba( white, 0 ),
		"border-color": #fff,
		"text-color": #fff,
		"hover-background-color": #fff,
		"hover-border-color": null,
		"hover-text-color": $primary-color
	)
);

// Solid Buttons
$btn-solid-variants: (
	// .#{$btn-class}
	"neutral": (
		"background-color": $gray-xl,
		"border-color": $gray,
		"text-color": $gray-xd,
		"hover-background-color": $gray-xl,
		"hover-border-color": $gray,
		"hover-text-color": $gray-xd
	),

	// .#{$btn-class}--primary
	"primary": (
		"background-color": $primary-color,
		"border-color": $primary-color,
		"text-color": #fff,
		"hover-background-color": $primary-color-d,
		"hover-border-color": $primary-color-d,
		"hover-text-color": #fff
	),

	// .#{$btn-class}--secondary
	"secondary": (
		"background-color": $secondary-color,
		"border-color": $secondary-color,
		"text-color": #fff,
		"hover-background-color": $secondary-color-d,
		"hover-border-color": $secondary-color-d,
		"hover-text-color": #fff
	),

	// .#{$btn-class}--tertiary
	"tertiary": (
		"background-color": $tertiary-color,
		"border-color": $tertiary-color,
		"text-color": #fff,
		"hover-background-color": $tertiary-color-d,
		"hover-border-color": $tertiary-color-d,
		"hover-text-color": #fff
	)
);

/**
 * Mixin for base button styles shared across all buttons.
 *
 * @since 1.0.0
 */
@mixin button {
	@extend %#{$btn-class}-text;
	display: inline-block;
	line-height: 1.25;
	padding: rem($btn-vertical-padding) rem($btn-horizontal-padding);
	text-align: center;
	text-decoration: none;
	transition-property: background-color, border-color, color;

	@if $btn-border-radius {
		border-radius: rem($btn-border-radius);
	}

	@if $btn-border-width {
		border: rem($btn-border-width) solid transparent;
	}

	@if $btn-font-size {
		font-size: rem($btn-font-size);
	}

	@if $btn-font-weight {
		font-weight: $btn-font-weight;
	}

	@if $btn-min-width {
		min-width: rem($btn-min-width);
	}

	@if $btn-text-transform {
		text-transform: $btn-text-transform;
	}
}

/**
 * Mixin for creating button variations.
 *
 * Used for adding background colors, text colors, and other unique properties
 * of each button, like the base 'btn' class, or any spin-off classes like
 * 'btn--primary' or 'btn--primary-o'.
 *
 * @since 1.0.0
 *
 * @param $background-color
 * @param $border-color
 * @param $text-color
 * @param $hover-background-color
 * @param $hover-border-color
 * @param $hover-text-color
 */
@mixin button-variant($background-color: null, $border-color: null, $text-color: null, $hover-background-color: null, $hover-border-color: null, $hover-text-color: null) {

	@if $background-color {
		background-color: $background-color !important;
	}

	@if $border-color {
		border-color: $border-color !important;
	}

	@if $text-color {
		color: $text-color !important;
	}

	@if $hover-background-color or $hover-border-color or $hover-text-color {

		&:hover {
			@if $hover-background-color {
				background-color: $hover-background-color !important;
			}

			@if $hover-border-color {
				border-color: $hover-border-color !important;
			}

			@if $hover-text-color {
				color: $hover-text-color !important;
			}
		}
	}
}
