/**
 * Pretty checkbox / radio buttons.
 *
 * Depends on container (such as an `<li>` element),
 * with nested input + label structure. Hides input
 * and uses the pseudo :before, :after elements
 * on label for radio appearance.
 *
 * @package  Embark
 * @since    1.0.0
 */

// Shared container styles. This could be a `<li>` element.
%pretty-radio ,
%pretty-checkbox {
	// Styles the container.
	padding-top: rem(4);
	padding-bottom: rem(4);
	padding-left: rem(40);
	position: relative;

	// Shared label styles.
	label {
		cursor: pointer;

		// Background of psuedo input (offstate).
		&:after {
			background: $checkbox-color;
			@if ( $checkbox-border-color ) {
				border: rem($checkbox-border-width) solid $checkbox-border-color;
			} @else {
				border: rem($checkbox-border-width) solid rgba( $primary-color, 0 );
			}
			content: "";
			display: block;
			height: rem($checkbox-size);
			position: absolute;
				top: rem($checkbox-position-top);
				left: 0;
				z-index: 0;
			transition-property: background-color, border-color, box-shadow;
			width: rem($checkbox-size);
		}
	}
}

// Shared input styles. Hides the input to user, but is still accessible.
%pretty-radio [type="radio"] ,
%pretty-checkbox [type="checkbox"] {
	appearance: none;
	cursor: pointer;
	opacity: 0;
	outline: none;
	position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: 2;
	width: rem($checkbox-size);

	// Focus state.
	&:focus + label:after {
		border-color: $checkbox-checked-border-color;
		box-shadow: 0 0 rem(5) rgba(black, .3);
	}
}

// Checkbox-specific styles.
%pretty-checkbox {

	label {
		// Checkmark.
		&:before {
			content: "";
			display: block;
			height: rem(10);
			border: solid $checkbox-checked-color;
			border-width: 0 rem(2) rem(2) 0;
			opacity: 0;
			position: absolute;
				top: rem($checkbox-check-position-top);
				left: rem($checkbox-check-position-left);
				z-index: 1;
			transform: rotate(45deg);
			transition-duration: 100ms;
			transition-property: border-color;
			width: rem(5);
		}

		// Background.
		&:after {
			border-radius: rem($radius);
		}
	}

	[type="checkbox"] {

		&:checked {

			+ label {

				// Checked -- highlight background.
				&::after {
					background: $checkbox-checked-background-color;
					border-color: $checkbox-checked-border-color;
				}

				// Checked -- show checkmark.
				&::before {
					opacity: 1;
				}
			}
		}
	}
}

// Radio-specific styles.
%pretty-radio {

	label {

		&::before {
			content: " ";
			border-radius: 50%;
			height: rem($radio-center-size);
			width: rem($radio-center-size);
			position: absolute;
			top: 50%;
			left: rem(($checkbox-size - $radio-center-size) / 2);
			z-index: 1;
			transform: translateY(-50%);
		}
		// Background.
		&:after {
			border-radius: 50%;
			top: rem(9);
		}
	}

	[type="radio"] {

		// Show radio selection.
		&:checked {

			+ label {

				&::before {
					background: $checkbox-checked-color;
				}

				&::after {
					background: $checkbox-checked-background-color;
					border-color: $checkbox-checked-border-color;
					box-shadow: inset 0 0 0 rem(5) $checkbox-checked-background-color;
					opacity: 1;
				}
			}
		}
	}
}
