/**
 * LIST STYLES
 * Base styles for ul, ol, li and multi-column lists.
 *
 * @since 1.0.0
 */
dt {
	font-weight: 500;
	margin-bottom: rem(2);
}

dd {
	margin-bottom: rem(8);
}

/**
* Multi-Column Lists
*/
.two-column,
.three-column,
.four-column {

	li {
		break-inside: avoid;
	}

	@include breakpoint(sm) {
		column-gap: rem($gutter);
	}
}

.two-column {

	@include breakpoint(sm) {
		column-count: 2;
	}
}

.three-column {

	@include breakpoint(sm) {
		column-count: 3;
	}
}
