/**
 * HTML element
 *
 * @see     config/variables/__colors.scss     Color variables.
 * @see     config/variables/__typography.scss Font-size/line-height variables.
 *
 * @package Embark
 * @since   1.0.0
 */

html {
	box-sizing: border-box;
	color: $base-font-color;
	font-size: rem($base-font-size);
	line-height: $base-line-height;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	@if $sticky-footer {
		height: 100%;
	}

	@include jacket( non-critical ) {

		&.html--menu-open,
		&.html--menu-open body {
			height: 100%;
			overflow: hidden;
		}
	}
}

.sticky:before,
.sticky:after {
	content: "";
	display: table;
}
