/**
 * Tables
 *
 * @since 1.0.0
 */

table {
	border-collapse: collapse;
	width: 100%;
	margin-bottom: $gap-lg;

	&[class*="align"] {
		display: table; // overrides .aligncenter, .alignleft, .alignright WP classes
	}

	&.wp-block-table {
		@include block-spacing;
	}
}

th,
td {
	border-right: rem(2) solid rgba( black, .02 );
	
	&:last-child {
		border-right: none;
	}
	
}

th ,
td[data-head]:before {
	font-weight: 700;
	text-align: left;
}

th {
	padding: rem(16) rem(20);
}

td {
	padding: rem(10) rem(20);
}

thead {

	tr {
		background: $primary-color;
		color: white;
		text-transform: uppercase;
		letter-spacing: rem(.5);
	}
	
	th {
		border-color: rgba(white, .1);
		line-height: 1.3333333;
		vertical-align: middle;
	}

}

td[data-head]:before {
	color: $primary-color;
	font-size: rem(14);
	padding: 0 rem(10);
	text-transform: uppercase;
}

tr:nth-child(even) {
	background: #fafafa;
}