#{$input-text},
textarea,
select,
.uploader .filename {
	appearance: none;
	display: block;
	width: 100%;

	@if $input-background-color {
		background-color: $input-background-color;
	}

	@if $input-border {
		border: $input-border;
	}

	@if $input-radius {
		border-radius: rem($input-radius);
	}

	@if $input-text-color {
		color: $input-text-color;
	}

	@if $input-font-family {
		@include font-family( $input-font-family );
	}

	@if $input-font-size {
		font-size: rem($input-font-size);
	}

	@if $input-font-size-mobile {
		@include breakpoint(sm, max) {
			font-size: rem($input-font-size-mobile);
		}
	}

	@if $input-height {
		height: rem($input-height);
	}

	@if $input-vertical-padding and not $input-horizontal-padding {
		padding: rem($input-vertical-padding) 0;
	} @else if $input-horizontal-padding and not $input-vertical-padding {
		padding: 0 rem($input-horizontal-padding);
	} @else {
		padding: rem($input-vertical-padding) rem($input-horizontal-padding);
	}

	@if $input-focus-border-color {

		&:focus {
			border-color: $input-focus-border-color;
			outline: none;
		}
	}
}

input:not([type="checkbox"]):not([type="radio"]),
textarea,
select {

	+ label {

		@if $sublabel-text-color {
			color: $sublabel-text-color;
		}

		@if $sublabel-font-family {
			@include font-family( $sublabel-font-family );
		}

		@if $sublabel-font-size {
			font-size: rem($sublabel-font-size) !important;
		}

		@if $sublabel-margin-top {
			margin-top: rem($sublabel-margin-top);
		}

		@if $sublabel-margin-bottom {
			margin-bottom: rem($sublabel-margin-bottom);
		}
	}
}

input,
textarea {

	&::placeholder {
		color: $input-placeholder-text-color;
	}
}

input {

	&::-ms-clear {
		display: none;
	}
}

textarea {
	height: auto;
}

select {

	@if $select-padding-right {
		padding-right: rem($select-padding-right);
	}

	@if $select-use-css-caret {
		backface-visibility: hidden;
		&::-ms-expand {
			display: none;
		}

		// Arrow. Uses an inline SVG data URI, and does a str-replace for color hex codes from #000 to %23000.
		&:not([multiple]) {
			background-image: str-replace( url("data:image/svg+xml,%3Csvg width='999' height='583' viewBox='0 0 999 583' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M998.542 83.05c0 8.667-3.333 16.334-10 23l-466 466c-6.666 6.667-14.333 10-23 10-8.666 0-16.333-3.333-23-10l-466-466c-6.666-6.666-10-14.333-10-23 0-8.666 3.334-16.333 10-23l50-50c6.667-6.666 14.334-10 23-10 8.667 0 16.334 3.334 23 10l393 393 393-393c6.667-6.666 14.334-10 23-10 8.667 0 16.334 3.334 23 10l50 50c6.667 6.667 10 14.334 10 23z' fill-rule='nonzero' fill='#{$select-css-caret-color}'/%3E%3C/svg%3E"), "#", "%23" );
			background-position: right 16px center;
			background-repeat: no-repeat;
			background-size: $select-css-caret-size auto;
		}

	}

	// Multiple type select elements.
	&[multiple] {
		height: auto;
		padding: 0;

		option {
			@if $input-vertical-padding and not $input-horizontal-padding {
				padding: rem($input-vertical-padding) 0;
			} @else if $input-horizontal-padding and not $input-vertical-padding {
				padding: 0 rem($input-horizontal-padding);
			} @else {
				padding: rem($input-vertical-padding) rem($input-horizontal-padding);
			}
		}
	}
}

// Uniform.js uploader.
.uploader {
	cursor: pointer;
	overflow: hidden;
	position: relative;

	@if $input-height {
		height: rem($input-height);
	}

	// input (opacity: 0 to hide. should lay on top of the uniform fields)
	input {
		cursor: pointer;
		height: 100%;
		opacity: 0;
		position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 2;
		width: 100%;
	}

	.filename {
		cursor: default;
		overflow: hidden;
		@if $input-vertical-padding and not $input-horizontal-padding {
			padding: rem($input-vertical-padding) 0;
		} @else if $input-horizontal-padding and not $input-vertical-padding {
			padding: 0 rem($input-horizontal-padding);
		} @else {
			padding: rem($input-vertical-padding) rem($input-horizontal-padding);
		}
		padding-right: rem(124);
		position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
		text-overflow: ellipsis;
		transition-property: border-color, background, color;
		white-space: nowrap;
		width: 100%;
	}

	// upload button
	.action {
		@extend %btn;

		@if $input-border {
			border: $input-border;
		}
		@if $input-radius {
			border-top-right-radius: rem($input-radius);
			border-bottom-right-radius: rem($input-radius);
		}
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		font-size: rem(16);
		line-height: 1.4;
		position: absolute;
			// top: rem(1);
			// right: rem(1);
			// bottom: rem(1);
			top: 0;
			right: 0;
			bottom: 0;
		transition-property: border-color, background, color;
	}
}

[type="submit"],
.application_button {
	@extend %btn;
	@extend %btn--primary;
	appearance: none;
	cursor: pointer;
	height: auto;
}

button {

	&:active,
	&:focus {

		// Prevents button text from shifting on clicke in IE 11.
		span {
			position: relative;
				top: 0;
				left: 0;
		}
	}
}

[type="checkbox"],
[type="radio"] {
	display: inline-block;
	height: auto;
	width: auto;
}

.pretty-checkbox {
	@extend %pretty-checkbox;
}

.pretty-radio {
	@extend %pretty-radio;
}

label {
	display: inline-block;

	@if $label-font-size {
		font-size: rem($label-font-size);
	}

	@if $label-font-family {
		@include font-family( $label-font-family );
	}

	@if $label-text-color {
		color: $label-text-color;
	}

	@if $label-margin-bottom {
		margin-bottom: rem($label-margin-bottom);
	}
}

@include jacket( non-critical ) {

	#{$input-text},
	textarea {
		&:focus,
		&:active {
			outline: none;
		}
	}

	#{$input-text},
	textarea,
	select,
	.uploader {
		transition-property: color, background-color, border-color;
	}

	.uploader input:hover + .filename + .action {
		background-color: map-deep-get( $btn-solid-variants, "neutral", "hover-background-color");
	}

	select:focus,
	textarea:focus,
	.uploader input:focus + .filename,
	select:active,
	textarea:active,
	.uploader input:active + .filename {
		@if $input-focus-border-color {
			border-color: $input-focus-border-color;
		}
	}

	.uploader input:focus + .filename + .action,
	.uploader input:active + .filename + .action {
		background-color: map-deep-get( $btn-solid-variants, "neutral", "hover-background-color");

		@if $input-focus-border-color {
			border-color: $input-focus-border-color;
		}
	}

	#{$input-text} {
		&:focus,
		&:active {
			@if $input-focus-border-color {
				border-color: $input-focus-border-color;
			}
		}
	}
}

// Required span tags.
.required {
	@extend %required; // see config/variables/components/_forms-elements.scss
}

// Fix iOS border rounding on default search.
input[type="search"] {
	border-radius: 0;
	-webkit-border-radius: 0;
}
