$border-primary: rem(1) solid $primary-color;
$border-secondary: rem(1) solid $secondary-color;
$border-tertiary: rem(1) solid $tertiary-color;
$border-gray-xd: rem(1) solid $gray-xd;
$border-gray-d: rem(1) solid $gray-d;
$border-gray: rem(1) solid $gray;
$border-gray-l: rem(1) solid $gray-l;
$border-gray-xl: rem(1) solid $gray-xl;

$border-black-muted: rem(1) solid rgba( black, .2 );
$border-white-muted: rem(1) solid rgba( white, .2 );
