blockquote {
	border-left: solid rem(5) $primary-color;
	margin: 0;
	padding-left: rem(75);
	position: relative;

	&::before {
		color: $primary-color;
		content: "\f205";
		font-family: "dashicons";
		font-size: rem(60);
		line-height: 1;
		position: absolute;
			top: rem(-5);
			left: rem(5);
	}

	p {
		color: $primary-color;
		font-size: rem(24);
		line-height: 1.41;
		margin-bottom: $gap-md;
	}

	cite {
		color: theme-color("gray");
		font-style: normal;
		font-weight: 700;
	}
}