a {
	overflow-wrap: break-word;
	word-break: break-word;
    text-decoration: underline;

    // Fixes issues where text-decoration is applied to pseudo elements on IE 11.
    // https://github.com/emagineusa/embark/issues/25
	&:hover {
		text-decoration: none;

		&::after {
			text-decoration: underline;
		}

		&::after {
			text-decoration: none;
		}
    }

    // If link is for a phone number, disable on viewports larger than smartphone
    @include breakpoint(sm) {

		&[href^="tel:"],
		&[href^="Tel:"] {
            color: inherit;
            cursor: default;
            pointer-events: none;
            text-decoration: none;
        }

    }
}
