/**
 * Horizontal Rule
 *
 * @see ../config/variables/_typography.scss for heading font-size variables.
 *
 * @since 1.0.0
 */
hr {
	border: none;
	margin-top: $gap-sm;
	margin-bottom: $gap-sm;

	&::before {
		@include center;
		content: " ";
		display: block;
		border-bottom: $border-gray-l;
		height: rem(0);
		max-width: 100%;
	}

	.site-content > & {
		width: calc(100% - #{rem($gap-px--sm * 2)});

		&::before {
			max-width: rem($content-width-default);
		}
	}

	@include breakpoint(xs) {
		margin-top: 3vw;
		margin-bottom: 3vw;
	}

	@include breakpoint(md) {
		margin-top: $gap-md;
		margin-bottom: $gap-md;
	}
}
