/**
 * Wildcard selector
 *
 * Sets up box-sizing to inherit from the html element.
 * Adds base transition duration and timing function for
 * any element that needs to be transitioned using 
 * transition-property.
 *
 * @see     config/variables/_transitions.scss Transition variables.
 *
 * @package Embark
 * @since   1.0.0
 */

*,
*:after,
*:before {
	box-sizing: inherit;

	@include jacket(non-critical) {
		transition-duration: $transition-duration;
		transition-property: none;
		transition-timing-function: $transition-easing;
	}
}
