/**
 * Headings
 *
 * @see ../config/variables/_typography.scss for heading font-size variables.
 *
 * @since 1.0.0
 */

@each $el, $font-size, $line-height, $margin, $font-weight, $font-style, $color, $mobile-font-size in $headings {

	#{$el},
	.#{$el},
	%#{$el} {
		@if $color {
			color: $color;
		} @else {
			color: $gray-xd;
		}
		@if $font-size {
			font-size: em($font-size);
		}
		@if $font-style {
			font-style: $font-style;
		}
		@if $font-weight {
			font-weight: $font-weight;
		}
		@if $line-height {
			line-height: $line-height;
		}
		@if $margin {
			margin: $margin;
		}

		// Responsive considerations
		@if $mobile-font-size {
			@include breakpoint(xs, max) {
				font-size: em($mobile-font-size);
			}
		}

		@include jacket(frontend) {

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		@include jacket(non-critical) {

			a:hover {
				@if $color {
					color: darken($color, 5%);
				} @else {
					color: $secondary-color;
				}
			}
		}
	}
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
p {

	&.alignwide {
		@include aligndefault--left;
	}
}
