@mixin site-gutter($force: false) {
	@if ($site-gutter-base) {
		@if $force {
			padding-left: rem($site-gutter-base) !important;
			padding-right: rem($site-gutter-base) !important;
		} @else {
			padding-left: rem($site-gutter-base);
			padding-right: rem($site-gutter-base);
		}
	}

	@if ($site-gutter-xs) {
		@include breakpoint(xs) {
			@if $force {
				padding-left: rem($site-gutter-xs) !important;
				padding-right: rem($site-gutter-xs) !important;
			} @else {
				padding-left: rem($site-gutter-xs);
				padding-right: rem($site-gutter-xs);
			}
		}
	}

	@if ($site-gutter-sm) {
		@include breakpoint(sm) {
			@if $force {
				padding-left: rem($site-gutter-sm) !important;
				padding-right: rem($site-gutter-sm) !important;
			} @else {
				padding-left: rem($site-gutter-sm);
				padding-right: rem($site-gutter-sm);
			}
		}
	}

	@if ($site-gutter-md) {
		@include breakpoint(md) {
			@if $force {
				padding-left: rem($site-gutter-md) !important;
				padding-right: rem($site-gutter-md) !important;
			} @else {
				padding-left: rem($site-gutter-md);
				padding-right: rem($site-gutter-md);
			}
		}
	}

	@if ($site-gutter-lg) {
		@include breakpoint(lg) {
			@if $force {
				padding-left: rem($site-gutter-lg) !important;
				padding-right: rem($site-gutter-lg) !important;
			} @else {
				padding-left: rem($site-gutter-lg);
				padding-right: rem($site-gutter-lg);
			}
		}
	}
}

@mixin responsive-gutter-property($prop) {

	@if ($site-gutter-base) {
		#{$prop}: rem($site-gutter-base);
	}

	@if ($site-gutter-xs) {
		@include breakpoint(xs) {
			#{$prop}: rem($site-gutter-xs);
		}
	}

	@if ($site-gutter-sm) {
		@include breakpoint(sm) {
			#{$prop}: rem($site-gutter-sm);
		}
	}

	@if ($site-gutter-md) {
		@include breakpoint(md) {
			#{$prop}: rem($site-gutter-md);
		}
	}

	@if ($site-gutter-lg) {
		@include breakpoint(lg) {
			#{$prop}: rem($site-gutter-lg);
		}
	}
}

